import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import QvipleLoader from "../../optimized/Loader/QvipleLoader";
import BorderBottom from "../../optimized/Ui/Border/BorderBottom";
import CustomCheckbox from "../../optimized/Ui/CustomCheckbox/CustomCheckbox";
import { Fragment, useEffect, useState } from "react";
import FormFile from "../../optimized/Ui/Input/FormFile";
import FormCalendarInput from "../../optimized/Ui/Input/FormCalendarInput";
import CustomRadioButton from "../../optimized/Ui/CustomRadioButton/CustomRadioButton";
import CustomSelect from "../../optimized/Ui/Select/CustomSelect";
import FormTextarea from "../../optimized/Ui/Input/FormTextarea";
import Loading from "../../optimized/Loader/Loading";
import { useFileUpload } from "../../hooks/authentication-api";
import { useStudentEditProfileAboutDetail } from "../../optimized/Hooks/ApiHooks/InstituteStudent/institute-student-api";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../optimized/Utils/Functions/dateUtility";
import style from "../../optimized/Form/Form.module.css";
import FormInput from "../../optimized/Ui/Input/FormInput";
import FormCreateButton from "../../optimized/Ui/Button/FormCreateButton";
import { useStudentFormEdit } from "../../hooks/member_tab/class-api";
import {
  existingRequiredField,
  requiredField,
} from "../../optimized/Validation/functions/checkingRequiredField";
import StudentLoginDetails from "./StudentLoginDetails";
import { imageShowUrl } from "../../optimized/Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../optimized/Services/UrlConfig/AssestsBaseUrl";
import { Dialog } from "@mui/material";
import CustomCrop from "../../ImageCrop/CustomCrop";
const DynamicEditStudentForm = ({ openLoginDetails, onLoginDetailToggle }) => {
  const getQuery = useLocation();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [stateLabel, setStateLabel] = useState([]);
  const [state, setState] = useState({});
  const [dynamicState, setDynamicState] = useState({});
  const [notRequiredState, setNotRequiredState] = useState([]);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [activeStep, setActiveStep] = useState("");

  const [fileUpload] = useFileUpload();
  const [studentFormEdit] = useStudentFormEdit();
  const {
    studentEditProfileAboutDetail,
    studentEditProfileAboutDetailRefetch,
  } = useStudentEditProfileAboutDetail({
    data: {
      sid: getQuery.state?.sid,
    },
    skip: !getQuery.state?.sid,
  });

  useEffect(() => {
    if (studentEditProfileAboutDetail) {
      let lb = [];
      let k_lb = {};
      let dy = {};
      let rq = [];
      for (let i = 0; i < studentEditProfileAboutDetail?.length; i++) {
        let st = studentEditProfileAboutDetail[i];
        let obj = {
          section_name: st?.key,
          field_list: [],
        };
        for (let j = 0; j < st?.fields?.length; j++) {
          let ct = st?.fields[j];
          obj.field_list?.push({
            label: ct?.form_checklist_lable,
            placeholder: ct?.form_checklist_placeholder,
            type: ct?.form_checklist_typo,
            db_key: ct?.form_checklist_key,
            options: ct?.form_checklist_typo_option_pl,
            width: ct?.width,
          });
          if (ct?.form_checklist_key) k_lb[ct?.form_checklist_key] = ct?.value;
          if (ct?.form_checklist_key_status === "DYNAMIC") {
            dy[ct?.form_checklist_key] = ct?.value;
          }
          if (!ct?.form_checklist_required) {
            rq.push(ct?.form_checklist_key);
          }
        }
        lb.push(obj);
      }

      setStateLabel(lb);
      setState(k_lb);
      setDynamicState(dy);
      setNotRequiredState(rq);
    }
  }, [studentEditProfileAboutDetail]);

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [activeStep]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onCropFileUpload = (e, flow) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveStep(flow);
    }
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  const onUploadDocument = (e, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [flow]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onCalendarChoose = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(e),
    }));
  };

  const onPreview = (e) => {
    e.preventDefault();
    let validation = requiredField(state, notRequiredState);
    if (existingRequiredField(validation) && getQuery.state?.sid) {
      setDisabled((pre) => !pre);
      const editProfile = {
        student: { ...state },
        fileArray: [],
        student_dynamic_field: [],
      };
      for (let ob in dynamicState) {
        editProfile.student_dynamic_field.push({
          key: ob,
          value: state[ob],
        });
      }

      studentFormEdit({
        sid: getQuery.state?.sid,
        editProfile,
      })
        .then(() => {
          studentEditProfileAboutDetailRefetch();
          setDisabled((pre) => !pre);
          navigate(-1);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info(
  //   "studentApplyFormFieldDetail",
  //   getQuery.state
  //   // state
  //   // activeStep
  //   // notRequiredState
  //   // stateLabel
  // );

  return (
    <>
      <form className={style.fc_container} onSubmit={onPreview}>
        {stateLabel?.length > 0 ? null : <Loading />}
        {stateLabel?.map((sl, index) =>
          index === 0 ? (
            <section
              className={style.fcc_section}
              key={`${sl?.section_name}${index}`}
            >
              <div
                className={style.fccs_inner}
                style={{
                  justifyContent: "flex-end",
                  marginTop: "1rem",
                }}
              >
                {sl?.field_list?.map((fd, d_index) => (
                  <div className={style.fcci_container}>
                    <label
                      htmlFor={`${fd?.db_key}${d_index}`}
                      className={style.fcc_label}
                    >
                      <img
                        className={style.fcc_photo}
                        src={
                          state[fd?.db_key]
                            ? `${imageShowUrl}/${state[fd?.db_key]}`
                            : `${assestsFeedUrl}/feed-user-avatar.svg`
                        }
                        alt="student avatar"
                      />
                      <img
                        src="/images/user-pencil-edit-transparent.svg"
                        alt="edit icon"
                        className={style.fcce_icon}
                      />
                    </label>
                    <input
                      type="file"
                      id={`${fd?.db_key}${d_index}`}
                      onChange={(e) => onCropFileUpload(e, fd?.db_key)}
                      style={{ display: "none" }}
                    />
                    <p>
                      {fd?.label ?? ""}
                      {errorField[fd?.db_key] ? (
                        <span
                          style={{
                            color: "rgb(231, 75, 75)",
                          }}
                        >
                          * {t("required")}
                        </span>
                      ) : null}
                    </p>
                  </div>
                ))}
              </div>
            </section>
          ) : (
            <section
              className={style.fcc_section}
              key={`${sl?.section_name}${index}`}
            >
              <h4>{sl?.section_name}:</h4>
              <div className={style.fccs_inner}>
                {sl?.field_list?.map((fl, f_index) =>
                  fl?.type === "TEXT" ? (
                    <FormInput
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={state[fl?.db_key]}
                      onChange={onChange}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "TEXTAREA" ? (
                    <FormTextarea
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={state[fl?.db_key]}
                      onChange={onChange}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: "100%",
                      }}
                    />
                  ) : fl?.type === "NUMBER" ? (
                    <FormInput
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={state[fl?.db_key]}
                      onChange={onChange}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "SELECT" ? (
                    <CustomSelect
                      key={`${fl?.db_key}${f_index}`}
                      selectLabel={fl?.label}
                      selectedValue={fl?.placeholder}
                      defalutValue={state[fl?.db_key]}
                      onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                      options={fl?.options || []}
                      errorShow={errorField[fl?.db_key]}
                      customStyleContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                      customListStyle={{
                        position: "absolute",
                        width: "100%",
                        zIndex: "1",
                      }}
                    />
                  ) : fl?.type === "MULITSELECT" ? (
                    <CustomSelect
                      key={`${fl?.db_key}${f_index}`}
                      selectLabel={fl?.label}
                      selectedValue={fl?.placeholder}
                      defalutValue={state[fl?.db_key]}
                      onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                      options={fl?.options || []}
                      errorShow={errorField[fl?.db_key]}
                      customStyleContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                      customListStyle={{
                        position: "absolute",
                        width: "100%",
                        zIndex: "1",
                      }}
                    />
                  ) : fl?.type === "RADIO" ? (
                    <CustomRadioButton
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      options={["Java", "C++", "C", "C#"]}
                      value={state[fl?.db_key]}
                      onChange={onChange}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "CHECKBOX" ? (
                    <CustomCheckbox
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      options={fl?.options}
                      defaultValue={state[fl?.db_key]}
                      onChange={onChange}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "CALENDAR" ? (
                    <FormCalendarInput
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={slashDateFormat(state[fl?.db_key])}
                      onChange={(e) => onCalendarChoose(e, fl?.db_key)}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "FILE" ? (
                    <FormFile
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={
                        state[fl?.db_key]
                          ? {
                              key: state[fl?.db_key],
                            }
                          : ""
                      }
                      onChange={(e) => onUploadDocument(e, fl?.db_key)}
                      errorShow={errorField[fl?.db_key]}
                      customFormFileContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "CROPIMAGE" ? (
                    <Fragment key={`${fl?.db_key}${f_index}`}>
                      {fl?.form_checklist_key ===
                      "studentProfilePhoto" ? null : (
                        <FormFile
                          label={fl?.label}
                          placeholder={fl?.placeholder}
                          name={fl?.db_key}
                          type={fl?.type}
                          value={
                            state[fl?.db_key]
                              ? {
                                  key: state[fl?.db_key],
                                }
                              : ""
                          }
                          onChange={onChange}
                          errorShow={errorField[fl?.db_key]}
                          customFormFileContainer={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                          }}
                        />
                      )}
                    </Fragment>
                  ) : null
                )}
              </div>
              <BorderBottom />
            </section>
          )
        )}
        <FormCreateButton
          label="update"
          customStyle={{
            width: "100%",
          }}
        />
      </form>
      {disabled && <QvipleLoader />}
      {openLoginDetails && (
        <StudentLoginDetails onLoginDetailToggle={onLoginDetailToggle} />
      )}
      {openCrop && (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      )}
    </>
  );
};

export default DynamicEditStudentForm;
